/*
Txt: #e1e8ea
Background: #151c1e
Prim: #d81f0e
Second: #1e1715
Accent: #87260d
*/

*{
    color: #e1e8ea;
    background-color: transparent;
}

.primaryBut{
    background-color: #d81f0e;
    border: #d81f0e 3px solid;
    border-radius: 20px;
    margin: 0 auto;
    transition: 0.3s;
    cursor: pointer;
    text-align: center;
    height: 50px;
    padding: 10px;
    
}

.primaryBut h1, .secondaryBut h1{
    position: relative;
    top: -20px;
    padding: 0;
}

.secondaryBut{
    background-color: #1e1715;
    border: #1e1715 3px solid;
    border-radius: 20px;
    margin: 0 auto;
    transition: 0.3s;
    cursor: pointer;
    text-align: center;
    height: 50px;
    padding: 10px;
}

.progress{
    position: relative;
    overflow: hidden;
    width: 288px;
    height: 28px;
    border-radius: 10px;
    background-color: #87260d;
    margin: 0 auto;
    cursor: pointer;
}

.progressPercent{
    color: #F1EBF4;
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

body{
    background-color: #151c1e;
}

#holder{
    width: 500px;
    height: 300px;
    background-color: #1e1715;
    margin: 0 auto;
    border-radius: 50px;
}

.holder{
    position: relative;
    top: 200px;
}

#title{
    text-align: left;
    position: relative;
    top: -180px;
    left: 200px;
}

#cover{
    width: 150px;
    height: 150px;
    background-color: transparent;
    border-radius: 20px;
    position: absolute;
    top: 75px;
    margin-left: 20px;
}

#playProg{
    position: relative;
    top: -360px;
    left: 80px;
}

#btns{
    height: 70px;
    position: relative;
    top: -460px;
    left: 200px;
}

#playbtn, #prv, #nxt, #pausebtn{
    float: left;
}

#prv {
    margin-right: 20px;
}

#nxt {
    margin-left: 20px;
}

#coverFill{
    width: 100%;
    background-color: #87260d;
    border-radius: 20px;
}

#pausebtn {
    display: none;
}

#sound {
    position: absolute;
    top: 125px;
    left: -90px;
}